<template>
    <div class="Map-Box">
        <div class="seek" v-if="mapObj.isSearch">
            <div id="r-result">
                <input type="text" id="suggestId" size="20" placeholder="输入地址查询" style="width:350px;" v-model="myValue"/>
                <button @click="seek">搜索</button>
            </div>
        </div>
        <div class="map" :id="mapParam.mapId" ref="bmap" v-show="isInitMap==1"></div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                map: null,
                BMap: null,
                BMapLib: null,
                myValue: null,
                geoc: null,
                mapObj: {
                    Control: false,
                    centerAndZoom: 12,
                    isSearch: false,
                    isLocation: false
                },
                pointData: [],
                mapParam: {
                    mapId: "mapcontain",
                    isNavigationControl: false,
                    callBack: ""
                },
                searchData: ""
            }
        },
        props: ["isInitMap", 'mapID', 'otherObj'],
        mounted() {
            //初始化Map
            let that = this;
            this.mapParam.mapId = this.mapID || "mapcontain";
            if(this.otherObj){
                for(let key in this.otherObj){
                    this.mapObj[key] = this.otherObj[key];
                }
            }
            let ak = "KCzp8claYra8uYAvahElV9oKUT6j7Gx1";//localStorage.mapKey; //
            setTimeout(function () {
                initMap({
                    ak: ak,
                    errFunc: function () {
                        alert("地图初始化错误");
                    },
                    callback: function () {
                        if (that.isInitMap == 1) {
                            that.initGisMap();
                        }
                        that.$emit("initData"); //调用数据初始化
                    }
                });
            }, 10);
        },
        methods: {
            //初始化地图
            initGisMap: function () {
                let self = this;

                if (typeof this.map == 'undefined' || this.map == null) {
                    this.map = new BMap.Map(self.mapParam.mapId, {enableMapClick:false});  // 创建地图实例
                    if(!this.mapObj.Control){
                        //地图平移缩放控件，默认位于地图左上方，它包含控制地图的平移和缩放的功能。
                        // this.map.addControl(new BMap.NavigationControl({anchor: BMAP_ANCHOR_TOP_RIGHT}));
                    }

                    // this.map.addControl(new this.BMap.MapTypeControl()); //地图类型控件，默认位于地图右上方。
                    this.map.enableScrollWheelZoom();  // 开启鼠标滚轮缩放
                    this.map.enableKeyboard();         // 开启键盘控制
                    this.map.enableContinuousZoom();   // 开启连续缩放效果
                    this.map.enableInertialDragging(); // 开启惯性拖拽效果
                    this.geoc = new BMap.Geocoder();
                    this.map.addControl(new BMap.MapTypeControl({
                        mapTypes:[
                            BMAP_NORMAL_MAP,
                            BMAP_HYBRID_MAP
                        ]}));
                    //默认成都市
                    this.map.centerAndZoom(new BMap.Point("104.077122", "30.660312"), this.mapObj.centerAndZoom);
                    if(this.mapObj.isLocation){
                        let myCity = new BMap.LocalCity();
                        myCity.get(function (result) {
                            let cityName = result.name;
                            self.map.centerAndZoom(cityName, self.mapObj.centerAndZoom);
                        });
                    }
                }
                if(this.mapObj.isSearch){
                    this.search();
                }
            },
            //鼠标点击获取经纬度
            getLatLon: function () {
                let self = this;
                this.map.addEventListener("click", function (e) {
                    self.deletePoint();
                    self.createLable(e.point.lng, e.point.lat);
                    function getSite() {
                       return new Promise((resolve, reject)=>{
                            self.geoc.getLocation(e.point, function(rs){
                                //addressComponents对象可以获取到详细的地址信息
                                let addComp = rs.addressComponents;
                                let site = addComp.province + ", " + addComp.city + ", " + addComp.district + ", " + addComp.street + ", " + addComp.streetNumber;
                                resolve(site);
                            });
                        })
                    }
                    getSite().then((site) => {
                        self.$emit("getLngLat", {lng: e.point.lng, lat: e.point.lat, site: site})
                    });
                });
            },
            // 删除标注点
            deletePoint: function () {
                let allOverlay = this.map.getOverlays();
                for (let i = 0; i < allOverlay.length; i++) {
                    this.map.removeOverlay(allOverlay[i]);
                }
            },
            // 添加标注点
            createLable: function (lng, lat) {
                let marker = new BMap.Marker(new BMap.Point(lng, lat));
                this.map.addOverlay(marker);
            },
            setCenterPoint: function (point, size) {
                if(size){
                    this.map.centerAndZoom(new BMap.Point(point[0].lng, point[0].lat), size);
                }else{
                    this.deletePoint();
                    let pointData = [];
                    for (let i = 0; i < point.length; i++){
                        if(point[i].lng){
                            pointData.push(point[i]);
                        }
                    }
                    if(pointData.length == 0){
                        return;
                    }
                    this.map.panTo(new BMap.Point(pointData[0].lng, pointData[0].lat));
                    this.addPoint(pointData);
                }
                this.pointData = point;
            },
            addPoint: function (point) {
                let location_blue = new BMap.Icon("dist/img/smokeFeeling/location_blue.png", new BMap.Size(30,48));
                let location_grey = new BMap.Icon("dist/img/smokeFeeling/location_grey.png", new BMap.Size(30,48));
                let location_orange = new BMap.Icon("dist/img/smokeFeeling/location_orange.png", new BMap.Size(30,48));
                let location_red = new BMap.Icon("dist/img/smokeFeeling/location_red.png", new BMap.Size(30,48));
                let location_yellow = new BMap.Icon("dist/img/smokeFeeling/location_yellow.png", new BMap.Size(30,48));

                var setIcon = function (status) {
                    let value = parseInt(status);
                    switch (value){
                        case 1:
                            return location_blue;
                        case 2:
                            return location_grey;
                        case 3:
                            return location_orange;
                        case 4:
                            return location_red;
                        case 5:
                            return location_yellow
                        case 6:
                            return location_blue
                    }
                }
                for(let i = 0; i < point.length; i++){
                    if(point[i].lng && point[i].lat){
                        let pt = new BMap.Point(point[i].lng, point[i].lat);
                        let marker = new BMap.Marker(pt,{icon: setIcon(point[i].status)});  // 创建标注
                        marker.id = i;
                        marker.addEventListener("click",this.attribute);
                        this.map.addOverlay(marker);
                        let label = new BMap.Label(point[i].index,{offset:new BMap.Size(4,4)});
                        label.setStyle({
                            color : "#000",
                            fontSize : "12px",
                            lineHeight : "20px",
                            fontFamily:"微软雅黑",
                            border:"none",
                            backgroundColor: "none",
                            width: "20px",
                            textAlign: "center",
                            transform: 'scale(0.8)'
                        });
                        marker.setLabel(label);
                    }
                }
            },
            attribute: function (e) {
                if(this.mapID){
                    this.$emit("minMapPoint", this.pointData);
                }else{
                    this.$emit("MaxMapPoint", this.pointData[e.target.id]);
                }
            },
            seek: function () {
                if(!!this.myValue){
                    this.setPlace(this.myValue)
                }
            },
            search: function () {
                let self = this;
                let ac = new BMap.Autocomplete(    //建立一个自动完成的对象
                    {"input" : "suggestId"
                        ,"location" : self.map
                    });
                ac.addEventListener("onhighlight", function(e) {  //鼠标放在下拉列表上的事件
                    let str = "";
                    let _value = e.fromitem.value;
                    let value = "";
                    if (e.fromitem.index > -1) {
                        value = _value.province +  _value.city +  _value.district +  _value.street +  _value.business;
                    }
                    str = "FromItem<br />index = " + e.fromitem.index + "<br />value = " + value;

                    value = "";
                    if (e.toitem.index > -1) {
                        _value = e.toitem.value;
                        value = _value.province +  _value.city +  _value.district +  _value.street +  _value.business;
                    }
                    str += "<br />ToItem<br />index = " + e.toitem.index + "<br />value = " + value;
                });

                ac.addEventListener("onconfirm", function(e) {    //鼠标点击下拉列表后的事件
                    let _value = e.item.value;
                    self.myValue = _value.province +  _value.city +  _value.district +  _value.street +  _value.business;
                    self.setPlace(self.myValue);
                });
            },
            setPlace: function(myValue){
                let self = this;
                function myFun(){
                    let pp = local.getResults().getPoi(0).point;    //获取第一个智能搜索的结果
                    self.searchData = pp;
                    self.map.centerAndZoom(pp, 18);
                    self.createLable(pp.lng, pp.lat)
                    console.log(local.getResults().getPoi(0))
                    self.$emit("getLngLat", {lng: pp.lng, lat: pp.lat, site: local.getResults().getPoi(0).address})
                }
                let local = new BMap.LocalSearch(self.map, { //智能搜索
                    onSearchComplete: myFun
                });
                local.search(myValue);
            }
        }
    }
</script>

<style lang="scss">
    #suggestId{
        background: #fff;
        color: #000 !important;
    }
    .Map-Box{
        height: 100%;
        position: relative;
        .map {
            height: 100%;
            border-radius: 5px;
        }
        .seek{
            position: absolute;
            left: 1em;
            top: 1em;
            z-index: 10;
            height: auto;
            overflow: hidden;
        }
        /*.searchResultPanel{*/
            /*z-index: 100;*/
            /*width:350px;*/
        /*}*/
    }
    .anchorBL{
        display: none;
    }
    .tangram-suggestion-main{
        z-index: 10000;
    }

</style>



