<template>
    <div class="info-div Device-info" ref="trys" >
        <div class="info-header" style="left: 1.1em">
            <span class="callback" @click="resetComponent">{{type == 0 ? '烟感设备列表' : '烟感状态监测'}} </span>
            <span> > 烟感设备详情</span>
        </div>
        <div class="info-container" style="margin-top: 1em;height: calc(100% - 1em);">
            <div class="list-content list-contentB detail-content" style="height:calc(30% - 3em);;padding:0">

                <div class="list-header" style="padding: 1em 0">
                    <i class="hasIcon hasImg">
                        <img src="dist/img/widsomApp/basic.png" />
                    </i>基础信息
                    <button class="list-operate" @click="editDevice" v-if="controls[2]">
                        <i class="hasIcon ">
                            <icon name="edit" ></icon>
                        </i>编辑
                    </button>
                    <div class="left-action">
                        设备状态:
                        <button class="list-operate" @click="smokeDeviceStatus(1)" :class="{'normal':normal, 'defriend':!normal}" v-show="Finish"></button>
                        短信发送:
                        <button class="list-operate" @click="smokeDeviceStatus(2)" :class="{'message':message, 'ismessage':!message}" v-show="Finish"></button>
                    </div>
                </div>
                <div class="content-right">
                    <ul class="form-list" style="padding:1em">
                        <li v-for="(item,$index) in commInfo" style="margin:0!important">
                            <span class="label-span">{{item.text}}:</span>
                            <span class="label-span" style="max-width: calc(100% - 8em);" :title="item.value"> &nbsp;&nbsp;&nbsp;{{item.value}}</span>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="list-content list-contentB" style="font-size: 0.94em;height: calc(70% - 1em);padding:1em; display: flex">
                <div class="smokeDataLog">
                    <div class="list-header noborder-ListHeader" style="float: none">
                        刷新时间: {{!!powerDate.createTime ? getTrueDate(parseInt(powerDate.createTime)):'--'}}
                        <button class="list-operate" @click="getPower" style="float: none">
                            <i class="hasIcon">
                                <img src="dist/img/costControl/fresh.png" style="height: auto;">
                            </i>刷新
                        </button>
                    </div>
                    <div class="bottom-batter">
                        <div class="batter-panel">
                            <div class="batter-pro">
                                <span :class="setPowerClass.class" :style="{width: setPowerClass.perc+'%'}"></span>
                            </div>
                            <div class="batter-back"><label v-if="setPowerClass.perc!=0">{{setPowerClass.perc}}%</label></div>
                        </div>
                        <div class="title-panel">电池电量</div>
                    </div>
                    <div class="bottom-alarm">
                        <div class="alarm-panel">
                            <img src="dist/img/smokeFeeling/normal.png" alt="正常" v-if="powerDate.status == '1' || powerDate.status == '6'">
                            <img src="dist/img/smokeFeeling/drop-line-icon.png" alt="正常" v-if="powerDate.status == '2' || powerDate.status == '3'">
                            <img src="dist/img/smokeFeeling/alarm2.png" alt="正常" v-if="powerDate.status == '4' || powerDate.status == '5'">
                        </div>
                        <div class="title-panel" style="text-indent: 1em;margin-top: -0.5em">{{statusText}}</div>
                    </div>
                </div>
                <div class="box-table">
                    <div class="list-header noborder-ListHeader">
                        设备通信列表( 更新周期: 1h)
                    </div>
                    <div class="operate-div">
                        <template v-for="(item, index) in formList">
                            <selects ref="selects"  v-if="item.type == 'select'" :option="item.options" @getSelect="getFrom"></selects>
                        </template>
                    </div>
                    <div ref="myOwnTable" class="table-container" style="height:calc(100% - 3em)">
                        <tables ref="myTable"  class="dialogTable" v-if="tableShow" :tableOpt="dialogTable" style="margin:0em;width:100%;"></tables>
                    </div>
                </div>
            </div>
        </div>
        <dialogs type="alert" ref="alerts"></dialogs>
        <div v-if="showInfo">
            <operateDia @deviceData="submitDevice" ref="addEditPlan" operateFlag="1" :priceData="detailData"></operateDia>
        </div>
    </div>
</template>
<script>
    import operateDia from './DeviceList/addOrEdit.vue'
    export default{
        data() {
            return {
                tableShow: false, //table延迟加载
                normal: true,
                message: true,
                detailData: '',
                showInfo: false,
                Finish: true,
                TabEndDate: "",
                searchData: {
                    status: "0",
                    startDate: "",
                    endDate: "",
                    cno: ""
                },
                startMonthDate: "",
                startWeekDate: "",
                powerDate: {},
                dialogTable:{
                    column: [
                        {title: '更新时间', width: '140', field: 'createTime', textAlign: 'center',type: 'string',
                            formatter: function (val, index, row, obj) {
                                let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                                if(vals == ''){
                                    return '';
                                }
                                return obj.getTrueDate(parseInt(vals))
                            }},
                        {title: '电池电量(KW·h)', field: 'power', textAlign: 'center',  width: 120 },
                        {title: '设备状态', width: '120', field: 'status', textAlign: 'center',type: 'string',
                            formatter: function (val, index, row, obj) {
                                switch(val) {
                                    case '1':
                                        return `<div style="color: #2F7FC4">在线</div>`
                                    case '2':
                                        return `<div style="color: #9FA1A0">离线</div>`;
                                    case '3':
                                        return `<div>停用</div>`;
                                    case '4':
                                        return `<div style="color: #F4564D">告警</div>`;
                                    case '5':
                                        return `<div style="color: #E18137">欠压</div>`;
                                    case '6':
                                        return `<div style="color: #3967D8">启用</div>`;
                                }

                            }},
                    ],
                    method: 'post',
                    url: 'smoke/smokeDeviceStatusList',
                    queryParam: {},
                    pageSize: 20,
                    pageNumber: 1,
                    height: 250,
                    isExport: true,
                    exportURL: 'smoke/smokeDeviceStatusListDownload',
                }
            }
        },
        props: ['row', 'controls', 'type'],
        components: {
            operateDia
        },
        computed: {
            commInfo: function(){
                let LngLat = (this.detailData.lng || '--') + ', '+ (this.detailData.lat || '--')
                return [
                    {text: '设备编号', value: this.detailData.cno || '--'},
                    {text: '通信编号', value: this.detailData.commNo || '--'},
                    {text: '负责人', value: this.detailData.chargeName || '--'},
                    {text: '负责人电话', value: this.detailData.chargeContact || '--'},
                    {text: '小区名称', value: this.detailData.resident || '--'},
                    {text: '安装位置', value: this.detailData.installAddr || '--'},
                    {text: '经纬度', value: LngLat},
                    {text: '备注信息', value: this.detailData.remark || '--'},
                ]
            },
            formList: function () {
                let dates = this.initDate(true);
                return  [{
                    type: 'select',
                    options: {
                        width: '7em',
                        cs: 'client',
                        data: [
                            {text: '设备状态', value: 0},
                            {text: '在线', value: 1},
                            {text: '离线', value: 2},
                            {text: '停用', value: 3},
                            {text: '告警', value: 4},
                            {text: '欠压', value: 5},
                        ],
                        key: 'searchData.status'
                    }
                }, {
                    type: 'select',
                    options: {
                        width: '7em',
                        cs: 'client',
                        data: [
                            {text: '近一周', value: 1},
                            {text: '近一月', value: 2},
                        ],
                        key: 'searchData.date'
                    }
                }
                ]
            },
            setPowerClass: function () {
                let level = this.powerDate.level;
                switch (level){
                    case '1':
                        return {class:'span1', perc: 25};
                    case '2':
                        return {class:'span2', perc: 50};
                    case '3':
                        return {class:'span3', perc: 75};
                    case '4':
                        return {class:'span4', perc: 100};
                    default:
                        return {class:'span1', perc: 0};
                 }
            },
            statusText: function () {
                let status = this.powerDate.status
                switch(status) {
                    case '1':
                        return `在线`
                    case '2':
                        return `离线`;
                    case '3':
                        return `停用`;
                    case '4':
                        return `告警`;
                    case '5':
                        return `欠压`;
                    case '6':
                        return '启用';
                }
            }
        },
        methods: {
            deviceUpdate: function () {
                let self = this;
                self.getWebsocket({
                    keyMsg: 'autoUploadMsg',
                    sign: true,
                    funMsg: function (data) {
                        let datas = data.data;
                        self.compareData(datas)
                    }
                });
            },
            compareData: function (data) {
                if(data.code == 1 && data.dataSrc.cno == this.row.cno){
                    let row = {};
                    let params = JSON.parse(data.dataSrc.params);
                    row.createTime = new Date(data.dataSrc.dataTime).getTime();
                    for(let i = 0; i < params.length; i++){
                        if(params[i].readType == 16){
                            row.power = params[i].readValue;
                        }else if(params[i].readType == 17){
                            row.status = params[i].readValue;
                        }else if(params[i].readType == 35){
                            row.level = params[i].readValue;
                        }
                    }
                    console.log(row);
                    this.powerDate = row;
                    this.$refs.myTable.operateData(row, '' ,0)
                }
            },
            smokeDeviceStatus: function (flag) {
                let self = this, url
                let param = {
                    cno: self.row.cno
                }
                if(flag == 1){
                    url = 'smoke/smokeDeviceStatus';
                    param.status = self.normal ? 3 : 2;
                }else{
                    url = 'smoke/smokeDeviceIsSms';
                    param.isSms = self.message ? 0 : 1;
                }
                this.getRequest({
                    url: url,
                    method: 'post',
                    param: param,
                    success: function (data) {
                        if(flag === 1){
                            self.normal = !self.normal;
                        }else{
                            self.message = !self.message;
                            self.detailData.isSms = self.message;
                            console.log(self.message)
                        }
                        self.$refs.alerts.show({
                            msg: '设置成功'
                        });
                    },
                    error: function () {
                        self.$refs.alerts.show({
                            msg: data.message
                        });
                    }
                }, true)

            },
            editDevice: function () {
                this.$refs.addEditPlan.showOperate();
            },
            submitDevice: function (param) {
                let self = this;
                let alerts = self.$refs.alerts;
                self.getRequest({
                    url: 'smoke/smokeDeviceEdit',
                    method: 'post',
                    param: param,
                    success: function (data) {
                        alerts.show({
                            msg: '编辑成功'
                        });
                        self.getDetail(1);
                        self.$refs.addEditPlan.cancelDialog();
                    },
                    error: function (data) {
                        alerts.show({
                            msg: data.message
                        });
                    }
                })
            },
            getDetail: function () {
                let self = this;
                this.getRequest({
                    url: 'smoke/smokeDeviceDetail',
                    method: 'post',
                    param: {
                        cno: self.row.cno,
                    },
                    success: function (data) {
                        self.detailData = data.data;
                        self.detailData.isSms = data.data.isSms != '0';
                        self.normal = data.data.status != 3
                        self.message = data.data.isSms == 1
                        self.showInfo = true;
                    },
                    error: function (data) {
                        self.$refs.alerts.show({
                            msg: data.message
                        });
                    }
                }, true)
            },
            getPower: function () {
                let self = this;
                this.getRequest({
                    url: 'smoke/smokeDataLog',
                    method: 'post',
                    param: {
                        cno: self.row.cno,
                    },
                    success: function (data) {
                        self.powerDate = data.data;
                        if(!self.powerDate.status){
                            self.powerDate.status = self.row.status
                        }
                    },
                    error: function (data) {
                        self.$refs.alerts.show({
                            msg: data.message
                        });
                    }
                }, true)
            },
            getParams: function () {
                let self = this;
                let arr = this.getSearchData(self.searchData);
                this.searchTable(arr, 1);
            },
            getFrom: function (val) {
                let key = val.key.split(".");
                if(key[1] === 'date'){
                    if(val.val === 2){
                        this.searchData.startDate = this.startMonthDate;
                    }else{
                        this.searchData.startDate = this.startWeekDate;
                    }
                    this.getParams();
                }else{
                    if ( this[key[0]][val.key.split('.')[1]] !== val.val) {
                        this.getOption(val, true);
                        this.getParams();
                    }
                }
            },
            getWeekDate: function () {
                let nowdate = new Date();
                let oneweekdate = new Date(nowdate-7*24*3600*1000);
                let y = oneweekdate.getFullYear();
                let m = oneweekdate.getMonth()+1;
                m = m < 10 ? '0' + m : m;
                let d = oneweekdate.getDate();
                this.startWeekDate = y+'-'+m+'-'+d;
                console.log(this.startWeekDate)
            },
            windowChange: function () {
                let self = this;
                this.getHeight();
                window.onresize = function () {
                    self.getHeight();
                }
            },
            init: function () {
                let dates = this.initDate(true);
                this.getDetail();
                this.getPower();
                this.getWeekDate();
                this.deviceUpdate();
                this.dialogTable.isExport = this.controls[6];
                this.searchData.cno = this.row.cno;
                this.searchData.startDate = this.startMonthDate = dates.start;
                this.searchData.endDate = dates.end;
                this.TabEndDate =  dates.start
                this.dialogTable.queryParam = this.searchData;
                this.tableShow = true;
            },
            resetComponent: function () {
                let that = this;
                that.show = false;
                that.$emit('closeDiv');
                setTimeout(function () {
                    that.show = false;
                }, 0)
            }
        },
        mounted: function () {
            this.init();
            this.windowChange();
        }
    }
</script>
<style lang="scss">
    $headerColor:#44BBDD;
    $leftContentW: 16%;
    $leftContentH: 1em;
    .Device-info{
        .left-action{
            float: right;
            .list-operate{
                width: 5em;
                vertical-align: middle;
                position: relative;
                top: -2px;
                float: none;
                margin-right: 1em;
            }
            .message{
                background: url("/dist/img/smokeFeeling/open.png") no-repeat;
                background-size: 100% 100%;
            }
            .ismessage{
                background: url("/dist/img/smokeFeeling/closed.png") no-repeat;
                background-size: 100% 100%;
            }
            .normal{
                background: url("/dist/img/smokeFeeling/active.png") no-repeat;
                background-size: 100% 100%;
            }
            .defriend{
                background: url("/dist/img/smokeFeeling/stop.png") no-repeat;
                background-size: 100% 100%;
            }
        }
        .info-container{
            margin-top:5px
        }
        .smokeDataLog{
            width: 50%;
            height: 100%;
        }
        .box-table{
            height: 100%;
            width: 50%;
        }
        .bottom-batter {
            width: 50%;
            float: left;
            .batter-panel {
                margin: 0 auto;
                height: 11em;
                width: 21em;
                position: relative;
                margin-top: 5em;
                .batter-back {
                    height: 10.5em;
                    width: 19.5em;
                    background-image: url("/dist/img/smokeFeeling/bettry.png");
                    background-size: cover;
                    background-repeat: no-repeat;
                    text-align: center;
                    position: absolute;
                    top: 0;
                    left: 0;
                    line-height: 6em;
                    display: flex;
                    align-items: center;
                    justify-content: space-around;
                    label {
                        font-size: 1.6em;
                        color: #ffffff;
                        margin-top: 1em;
                    }
                }
                .batter-pro {
                    margin-left: 1em;
                    height: 7.6em;
                    width: 15.5em;
                    border: 2px solid rgba(204, 204, 204, 0.6);
                    background-position: 0.1em;
                    position: absolute;
                    top: 2em;
                    left: 16px;
                    overflow: hidden;
                    span {
                        display: block;
                        float: left;
                        height: 100%;
                    }
                    .span1 {
                        background: linear-gradient(to top, #e13636, #c31212);
                    }
                    .span2 {
                        background: linear-gradient(to top, #e46f40, #c34812);
                    }
                    .span3 {
                        background: linear-gradient(to top, #008dcf, #1261c3);
                    }
                    .span4 {
                        background: linear-gradient(to top, #59d99b, #12c38f);
                    }
                    span:not(:last-child) {
                        margin-right: 2px;
                    }
                }
            }
            .title-panel {
                padding: 0.5em;
                font-size: 1em;
                color: #ffffff;
                text-align: center;
            }
        }
        .bottom-alarm {
            width: 50%;
            float: left;
            .alarm-panel {
                margin: 0 auto;
                margin-top: 3em;
                width: 14em;
                height: 14.2em;
                img{
                    width: 14em;
                    height: 14.2em;
                }
            }
        }
    }
</style>



