<template>
    <div v-if="showDialog" class="smoke-AaddEdit">
        <dialogs type="customer" ref="addEquip" v-if="showDialog" width="50em" :hasNoPaddB='true'>
            <div slot="header">
                {{operateFlag == 0 ? '添加设备' : '修改设备'}}
            </div>
            <div slot="content" >
                <ul class="dialog-ul">
                    <li v-for="(item,$index) in addList" class=" formList " :class="item.classes">
                        <label v-if="!item.isEmpty" class="label-show" style="margin-left: 0;"><span class="emptyTips"> * </span> {{item.label}} </label>
                        <label v-if="item.isEmpty && item.label" class="label-show" style="margin-left: 0;"><span >&nbsp;</span>{{item.label}} </label>
                        <inputs v-if="item.type === 'input'" :isEmpty="item.isEmpty"  :option="item.options" @getInput="getFrom" :ref="item.options.key"></inputs>
                        <selects v-if="item.type === 'select'" :option="item.options" @getSelect="getFrom" :ref="item.options.key"></selects>
                        <datePicker v-if="item.type === 'timer'" :dateOpt="item.options" @getDatePicker="getFrom" :ref="item.options.key"></datePicker>
                        <button v-if="item.type === 'button'" class="button-default" @click="changeGps" style="margin: 0">选择坐标信息</button>
                    </li>
                    <li style="text-align: left">
                        <label class="label-show" style="margin-left: 0;"><span class="emptyTips">   </span> </label>
                        <input type="checkbox"  class="checkBox dialogFromCheck"  style="float:left" @click="hasMonthPlan($event)" :checked="checked" />
                        <span class="inputChoose myCheckbox myCheckBox dialogFromCheckSpan">发送短信</span>
                    </li>
                </ul>
            </div>
            <div slot="footer">
                <button class="button-default" @click="submitDevice">提交</button>
                <button class="button-default cancel-button" @click="cancelDialog">取消</button>
            </div>
        </dialogs>
        <dialogs type="customer" ref="coordinate" width="70em" :hasNoPaddB='true' height="40em">
            <div slot="header">选择经纬度</div>
            <div slot="content" style="height: 100%;width: 100%">
                <div class="map-box" v-if="showMap">
                    <baiduMap ref="map"  class="mapPanel" isInitMap="1" @initData="initMapData" @getLngLat="getLngLat" :otherObj="otherObj"></baiduMap>
                </div>
                <p style="text-align: left"><span v-html="mapLagLng"></span></p>
            </div>
            <div slot="footer">
                <button class="button-default" @click="submitMap">确定</button>
                <button class="button-default cancel-button" @click="cancelMap">取消</button>
            </div>
        </dialogs>
        <dialogs type="alert" ref="alerts" ></dialogs>
    </div>
</template>
<script>
    import 'vue-awesome/icons/plus-circle'
    import baiduMap from './../../childComponent/baiduMap.vue'
    export default{
        data() {
            return {
                showDialog: false,
                mapLagLng: '',
                checked: false,
                showMap: false,
                otherObj: {
                    isSearch: true,
                    isLocation: false,
                },
                formData: {
                    "cno": "",
                    "commNo": "",
                    "resident": "",
                    "installAddr": "",
                    "lng": "",
                    "lat": "",
                    "chargeName": "",
                    "chargeContact": "",
                    "isSms": 0,
                    "remark": "",
                }
            }
        },
        props: ['priceData', 'operateFlag'],
        computed: {
            addList: function () {
                let width = 'calc(100% - 6.5em)';
                let self = this;
                return [
                    {
                        classes: 'halfWidth',
                        type: 'input',
                        isEmpty: false,
                        label: '设备编号',
                        options: {
                            type: self.operateFlag==1?'disabledInput':'normalInput',
                            width: width,
                            limit: 'number',
                            placeHolder: '1-16位数字',
                            value: this.formData.cno,
                            length: 16,
                            key: 'formData.cno'
                        }
                    },
                    {
                        classes: 'halfWidth hasMarginLi',
                        type: 'input',
                        isEmpty: false,
                        label: '通信编号',
                        options: {
                            type: self.operateFlag==1?'disabledInput':'normalInput' ,
                            placeHolder: '1-30位（数字和字母组合）',
                            value: this.formData.commNo,
                            width: width,
                            limit: 'NAC',
                            length: 30,
                            key: 'formData.commNo'
                        }
                    },{
                        classes: 'halfWidth',
                        type: 'input',
                        isEmpty: true,
                        label: '负责人姓名',
                        options: {
                            type: 'normalInput',
                            value: this.formData.chargeName,
                            width: width,
                            regular: 'name',
                            key: 'formData.chargeName',
                            placeHolder: '可包含中英文、下划线',
                            length: 20,
                            errorMsg: '人员姓名：不允许输入@、#等特殊字符'
                        }
                    },{
                        classes: 'halfWidth hasMarginLi',
                        type: 'input',
                        isEmpty: true,
                        label: '负责人电话',
                        options: {
                            type: 'normalInput',
                            value: this.formData.chargeContact,
                            width: width,
                            regular: 'phone',
                            key: 'formData.chargeContact',
                            placeHolder: '11位数字',
                            length: 13,
                            errorMsg: '联系方式为11位手机号或者座机号',
                            limit: 'phone'
                        }
                    }, {
                        classes: 'halfWidth ',
                        type: 'input',
                        isEmpty: false,
                        label: '小区信息',
                        options: {
                            type: 'normalInput',
                            width: width,
                            value: this.formData.resident,
                            length: 200,
                            key: 'formData.resident'
                        }
                    },
                    {
                        classes: 'halfWidth hasMarginLi',
                        type: 'input',
                        isEmpty: false,
                        label: '安装位置',
                        options: {
                            type: 'normalInput',
                            width: width,
                            value: this.formData.installAddr,
                            key: 'formData.installAddr'
                        }
                    },{
                        classes: 'specialWidth',
                        type: 'input',
                        isEmpty: true,
                        label: 'GPS经度',
                        options: {
                            type: 'normalInput',
                            dataType: '+float',
                            limit: 'float',
                            value: this.formData.lng,
                            width: 'calc(100% - 6.5em)',
                            key: 'formData.lng'
                        }
                    },{
                        classes: 'specialWidth hasMarginLi',
                        type: 'input',
                        isEmpty: true,
                        label: 'GPS纬度',
                        options: {
                            type: 'normalInput',
                            dataType: '+float',
                            limit: 'float',
                            value: this.formData.lat,
                            width: 'calc(100% - 6.5em)',
                            key: 'formData.lat'
                        }
                    },{
                        classes: 'addLat',
                        type: 'button',
                        isEmpty: true,
                    },{
                        classes: 'allWidth',
                        type: 'input',
                        isEmpty: true,
                        label: '备注信息',
                        options: {
                            type: 'textArea',
                            value: this.formData.remark,
                            width: width,
                            placeHolder: '50个字符内',
                            key: 'formData.remark'
                        }
                    }
                ]
            },
        },
        components: {
            baiduMap
        },
        methods: {
            submitMap: function () {
                this.$refs.coordinate.close()
                this.showMap = false;
            },
            cancelMap: function () {
                this.$refs.coordinate.close();
                this.showMap = false;
            },
            changeGps: function () {
                this.$refs.coordinate.show()
                if(this.operateFlag == 0){
                    this.otherObj.isLocation = true;
                }
                if(this.formData.lng && this.formData.lat){
                    this.otherObj.isLocation = false;
                }
                this.showMap = true;
            },
            getLngLat: function (obj) {
                this.mapLagLng = `经度: ${obj.lng}, 维度: ${obj.lat}; 地址: ${obj.site.split(", ").join("")}`;
                this.formData.lng = obj.lng;
                this.formData.lat = obj.lat;
            },
            initMapData: function () {
                let self = this;
                this.$refs.map.getLatLon();
                let setCore = function (lng, lat) {
                    self.$refs.map.setCenterPoint([{
                        lng: lng,
                        lat: lat,
                    }], 19);
                    self.$refs.map.createLable(lng, lat)
                };
                if(this.operateFlag == 1){
                    setCore(self.priceData.lng, self.priceData.lat)
                }
                if (this.formData.lng && this.formData.lat){
                    setCore(this.formData.lng, this.formData.lat)
                }
            },
            hasMonthPlan: function (event) {
                this.checked = event.target.checked;
            },
            showOperate: function() {
                this.showDialog = true;
                let self = this;
                this.copyData()
                setTimeout(function () {
                    self.$refs.addEquip.show()
                }, 0)
            },
            submitDevice: function () {
                let self = this;
                let alerts = self.$refs.alerts;
                self.regularTest({
                    emptyKey: ['cno', 'commNo', 'resident','installAddr'],
                    prefix: 'formData',
                    callback: function () {
                        if(parseFloat(self.formData.lng) > 180) {
                            alerts.show({msg: '经度应该小于180 !'});
                            return
                        }
                        if(parseFloat(self.formData.lat) > 90) {
                            alerts.show({msg: '纬度应该小于90 !'});
                            return
                        }
                        let param = self.formData;
                        param.isSms = self.checked ? 1 : 0;
                        self.$emit('deviceData', self.getSearchData(param));
                    }
                })
            },
            cancelDialog: function () {
                this.formData = this.cleanSearchData(this.formData)
                this.$refs.addEquip.close()
                this.showDialog = false;
                this.mapLagLng = "";
            },
            getFrom: function (val) {
                this.getOption(val, true);
            },
            copyData: function () {
                if(this.operateFlag == 1){
                    for(let key in this.formData) {
                        this.formData[key] = this.priceData[key] || ''
                    }
                    this.checked = this.priceData.isSms
                }
            }
        },
        created: function () {
            this.copyData()
        },
        watch: {
            priceData: {
                deep: true,
                handler: function (newVal,oldVal){
                    this.copyData();
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .smoke-AaddEdit{
        .dialog-ul .hasCheck-allWidth {
            width: fit-content!important;
            width:  -webkit-fit-content!important;
            width:  -moz-fit-content!important;
            width:max-content;
            float: left;
            .inputChoose{
                line-height: 2.5;
                padding-left: 1em;
            }
            input.checkBox{
                left:0.7em
            }
        }
        .map-box{
            height: calc(100% - 2em);
            width: 100%;
            position: relative;
        }
        .dialog-ul {
            .addLat{
                float: right;
                width: 5em;
                margin: 0;
                min-width: 10em !important;
            }
            .specialWidth{
                width: calc(50% - 5.5em - 1em) !important;
                float: left;
                margin-left: 6px;
            }
            .label-show{
                min-width: 5.5em;
            }
            .myCheckbox:before{
                left: -1.5em;
            }
        }
    }
</style>



