<template>
    <div ref="trys" style="height: 100%">
        <div v-if="showInfo" class="info-container">
            <info :row="row" @closeDiv="closeDiv" :controls="controls" ref="infoDetail" type="0"></info>
        </div>
        <div  v-show="!showInfo"  class="all-container">
            <div class="operate-div operateBtn">
                <button class="button-operate" @click="addEquipment" v-if="controls[1]">
                    <i class="hasIcon hasImg">
                        <icon name="plus-circle" scale="1.4"></icon>
                    </i>添加
                </button>
                <button class="button-operate" @click="removeEquipment" v-if="controls[3]">
                    <i class="hasIcon hasImg">
                        <icon name="trash" scale="1.4"></icon>
                    </i>删除
                </button>
            </div>
            <div class="operate-div">
                <template v-for="(item, $index) in formList">
                    <selects ref="selects"  v-if="item.type == 'select'" :option="item.options" @getSelect="getFrom"></selects>
                    <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom"></inputs>
                </template>
            </div>
            <div ref="myOwnTable" class="table-container" >
                <tables ref="myTable" :tableOpt="tableOpt" class="tablePadding" v-if="tableShow"
                        @rowDbClick="checkInfo"></tables>
            </div>
        </div>
        <dialogs type="confirm" ref="conformDialog"></dialogs>
        <dialogs type="alert" ref="alerts"></dialogs>
        <operateDia @deviceData="submitDevice" ref="addEditPlan" operateFlag="0"></operateDia>
    </div>
</template>

<script>
    import 'vue-awesome/icons/plus-circle'
    import info from './../info.vue'
    import operateDia from './addOrEdit.vue'
    export default{
        data() {
            return {
                showInfo: false,
                selected: [],
                searchParam: {
                    cno: "",
                    status: 0
                },
                row: '',
                controls: [],
                tableOpt: {
                    column: [
                        {field: 'state',type: 'checkBox', textAlign: 'center',  width: '30'},
                        {title: '序号', field: 'Index',textAlign: 'center',width: '30'},
                        {title: '状态', field: 'status', textAlign: 'center', width: '50',type: 'string',
                            formatter: function (val, index, row, obj) {
                                let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                                switch(vals) {
                                    case '1':
                                        return `<div style="color: #2F7FC4">在线</div>`
                                    case '2':
                                        return `<div style="color: #9FA1A0">离线</div>`;
                                    case '3':
                                        return `<div>停用</div>`;
                                    case '4':
                                        return `<div style="color: #F4564D">告警</div>`;
                                    case '5':
                                        return `<div style="color: #E18137">欠压</div>`;
                                    case '6':
                                        return `<div style="color: #3967D8">启用</div>`;
                                }

                            }},
                        {title: '设备编号', field: 'cno', textAlign: 'center', width: '90'},
                        {title: '通信编号', field: 'commNo', textAlign: 'center', width: '100'},
                        {title: '小区信息', field: 'resident', textAlign: 'center', width: '120'},
                        {title: '安装位置', field: 'installAddr', textAlign: 'center', width: '250'},
                        {title: '安装时间', field: 'createTime', textAlign: 'center', width: '250',type: 'string',
                            formatter: function (val, index, row, obj) {
                                let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                                if(vals == ''){
                                    return '';
                                }
                                return obj.getTrueDate(parseInt(vals))
                            }}
                    ],
                    url: 'smoke/smokeDeviceList',
                    method: 'post',
                    pageSize: 20,
                    pageNumber: 1,
                    pageList: [20, 40, 60],
                    queryParam: {},
                    isExport: true,
                    exportURL: 'smoke/smokeDeviceListDownload'
                },
                tableShow: false,
            }
        },
        computed: {
            formList: function () {
                return [
                    {
                        type: 'select',
                        options: {
                            width: '7em',
                            cs: 'client',
                            data: [
                                {text: '设备状态', value: 0},
                                {text: '在线', value: 1},
                                {text: '离线', value: 2},
                                {text: '停用', value: 3},
                                {text: '告警', value: 4},
                                {text: '欠压', value: 5},
                            ],
                            key: 'searchParam.status'
                        }
                    }, {
                        type: 'input',
                        options: {
                            type: 'typeSearch',
                            key: 'searchParam',
                            selectOpt: {
                                width: '7em',
                                cs: 'client',
                                data: [
                                    {text: '设备编号', value: 'cno'}
                                ]
                            }
                        }
                    }
                ]
            },
        },
        components: {
            operateDia,
            info
        },
        methods: {
            submitDevice: function (param) {
                let self = this;
                let alerts = self.$refs.alerts;
                self.getRequest({
                    url: 'smoke/smokeDeviceAdd',
                    method: 'post',
                    param: param,
                    success: function (data) {
                        self.$refs.alerts.show({
                            msg: "添加成功"
                        });
                        self.getParams(1);
                        self.$refs.addEditPlan.cancelDialog();
                    },
                    error: function (data) {
                        self.$refs.alerts.show({
                            msg: data.message
                        });
                    }
                })
            },
            checkInfo: function (val) {
                this.row = val.val;
                this.showInfo = true;
            },
            closeDiv: function () {
                this.showInfo = false;
                this.getParams();
                this.$store.state.callback.autoUploadMsg = null;
            },
            //添加设备
            addEquipment: function () {
               this.$refs.addEditPlan.showOperate();
            },
            //删除设备
            removeEquipment: function () {
                let that = this;
                this.getConfirm({
                    emptyMsg: '请先选择要删除的设备!',
                    confirmMsg: '确定要对选中的设备进行删除操作吗？',
                    callback: {
                        confirm: function () {
                            that.deleteDevice();
                        }
                    }
                });
            },
            deleteDevice: function () {
                let that = this;
                this.deleteData({
                    url: 'smoke/smokeDeviceDelete',
                    params: ['cno'],
                    callback: function (data) {
                        that.getParams(1);
                    },
                    error: function (data) {

                    }
                })
            },
            getFrom: function (val) {
                if (val.combine) {
                    if ( this.searchParam[val.key.split('.')[1]] !== val.val) {
                        this.getOption(val, true);
                        this.getParams(1);
                    }
                } else {
                    if ( this.searchParam[val.key.split('.')[1]] !== val.val) {
                        this.getOption(val, true);
                        this.getParams();
                    }
                }
            },
            getParams: function (pageNumber) {
                let arr = this.getSearchData(this.searchParam);
                this.getHeight();
                this.searchTable(arr, pageNumber);
            },
            resizeCon: function () {
                this.getHeight();
            },
            windowChange: function () {
                this.resizeCon();
                let that = this;
                window.onresize = function () {
                    that.resizeCon();
                }
            },
            init: function () {
                let that = this;
                this.controls[1] = true;
                this.controls[2] = true;
                this.controls[3] = true;
                this.controls[6] = true;
                this.tableOpt.isExport = this.controls[6];
                this.tableOpt.queryParam = this.searchParam
                this.tableShow = true;
                window.onresize = function () {
                    that.windowChange()
                }
            },
        },
        beforeRouteEnter(to, from, next) {
            next(vm => {
                if(vm.showInfo) {
                    let obj = vm.$refs.infoDetail;
                    obj.init();
                }
            });
        },
        beforeDestroy(){
            this.$store.state.callback.autoUploadMsg = null;
        },
        created: function () {
            this.isIn();
            this.getAction();
        },
    }
</script>

<style lang="scss">
    .dialog-ul .hasCheck-allWidth {
        width: fit-content!important;
        width:  -webkit-fit-content!important;
        width:  -moz-fit-content!important;
        width:max-content;
        float: left;
        .inputChoose{
            line-height: 2.5;
            padding-left: 1em;
        }
        input.checkBox{
            left:0.7em
        }
    }
    .map-box{
        height: 100%;
        width: 100%;
    }
</style>


